import React from "react";
import "./index.css";
import CartDetail from "./CartDetail";
import Navigation from "./Navigation";
import Total from "./Total";
import RemoveItem from "./RemoveItem";

const Cart = () => {
  return (
    <>
      <Navigation />
      <div className="cart">
        {CartDetail.map((v, i) => {
          return (
            <>
              <div className="cartitem">
                <img src={v.image} alt={v.name.split(" ").join("-")} />
                <div className="content">
                  <h3>{v.name}</h3>
                  <p>Company : {v.company}</p>
                  <p>
                    Price : Rs. <b>{v.price}</b>
                  </p>
                  {console.log(i)}
                  <RemoveItem key={i} remove={i} />
                </div>
              </div>
            </>
          );
        })}
      </div>
      <Total />
    </>
  );
};

export default Cart;
