import React from "react";
import { Link } from "react-router-dom";
import "./index.css";
import "bootstrap/dist/css/bootstrap.min.css";
import { Navbar, Nav, Dropdown } from "react-bootstrap";
import ShoppingCartIcon from "@material-ui/icons/ShoppingCart";
import CartDetail from "./CartDetail";

const Navigation = props => {
  console.log(props.l);
  console.log(CartDetail.length);
  return (
    <>
      <Navbar bg="light" expand="lg">
        <Navbar.Brand>
          <Link style={{ textDecoration: "none" }} to="/">
            Harshil Chudasama
          </Link>
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="ml-auto">
            <Link className="links" to="/">
              Home
            </Link>
            <Link className="links" to="/about">
              About
            </Link>
            <Dropdown>
              <Dropdown.Toggle
                className="bg-light dropdown"
                style={{
                  border: "none",
                  padding: 0,
                  margin: 0,
                  color: "#0275d8"
                }}
                id="dropdown-basic"
              >
                <Link>Products</Link>
              </Dropdown.Toggle>
              <Dropdown.Menu>
                <Dropdown.Item>
                  <Link classname="links" to="/products">
                    All Products
                  </Link>
                </Dropdown.Item>
                <Dropdown.Item>
                  <Link classname="links" to="/Realme">
                    Realme
                  </Link>
                </Dropdown.Item>
                <Dropdown.Item>
                  <Link classname="links" to="/Xiaomi">
                    Xiaomi
                  </Link>
                </Dropdown.Item>
                <Dropdown.Item>
                  <Link classname="links" to="/Samsung">
                    Samsung
                  </Link>
                </Dropdown.Item>
                <Dropdown.Item>
                  <Link classname="links" to="/Apple">
                    Apple
                  </Link>
                </Dropdown.Item>
                <Dropdown.Item>
                  <Link classname="links" to="/OnePlus">
                    OnePlus
                  </Link>
                </Dropdown.Item>
              </Dropdown.Menu>
            </Dropdown>
            <Link className="links" to="/contact">
              Contact
            </Link>
            <Link className="links" to="/cart">
              <ShoppingCartIcon />
              <span style={{ transform: "scale(1.3)" }} class="badge bg-light">
                {CartDetail.length}
              </span>
            </Link>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
    </>
  );
};

export default Navigation;
