import React from "react";
import "./index.css";
import CartDetail from "./CartDetail";

const Total = () => {
  let total = 0;
  return (
    <>
      {CartDetail.map(v => {
        return <span style={{opacity: 0,display : 'none'}}>{total = total + v.price}</span>
      })}
      <h1>Total : {total}</h1>
    </>
  );
};

export default Total;
