import React from "react";
import "./index.css";
import { Link } from "react-router-dom";

const Card = props => {
  return (
    <>
      <div className="card">
        <Link to={`/${props.company}/${props.name.split(" ").join("-")}`}>
          <img src={props.image} alt={props.name.split(" ").join("-")} />
        </Link>
        <h6>{props.name}</h6>
        <p>{props.company}</p>
        <p>
          <b>Rs. {props.price}</b>
        </p>
      </div>
    </>
  );
};

export default Card;
