import React, { useState } from "react";
import "./index.css";
import Mobiles from "./Mobiles";
import "bootstrap/dist/css/bootstrap.min.css";
import Card from "./Card";
import Navigation from "./Navigation";
function allcode() {
  return (
    <>
      {Mobiles.map((v, i) => {
        return (
          <Card
            key={i}
            image={v.image}
            name={v.name}
            company={v.company}
            price={v.price}
          />
        );
      })}
    </>
  );
}
function l10code() {
  return (
    <>
      {Mobiles.map((v, i) => {
        if (v.price <= 10000)
          return (
            <Card
              key={i}
              image={v.image}
              name={v.name}
              company={v.company}
              price={v.price}
            />
          );
        else return false;
      })}
    </>
  );
}
function g10code() {
  return (
    <>
      {Mobiles.map((v, i) => {
        if (v.price >= 10000 && v.price < 25000)
          return (
            <Card
              key={i}
              image={v.image}
              name={v.name}
              company={v.company}
              price={v.price}
            />
          );
        else return false;
      })}
    </>
  );
}
function g25code() {
  return (
    <>
      {Mobiles.map((v, i) => {
        if (v.price >= 25000 && v.price < 50000)
          return (
            <Card
              key={i}
              image={v.image}
              name={v.name}
              company={v.company}
              price={v.price}
            />
          );
        else return false;
      })}
    </>
  );
}
function g50code() {
  return (
    <>
      {Mobiles.map((v, i) => {
        if (v.price >= 50000)
          return (
            <Card
              key={i}
              image={v.image}
              name={v.name}
              company={v.company}
              price={v.price}
            />
          );
        else return false;
      })}
    </>
  );
}

const Products = () => {
  const [show, setShow] = useState(allcode());
  function l10() {
    setShow(l10code());
  }
  function g10() {
    setShow(g10code());
  }
  function g25() {
    setShow(g25code());
  }
  function g50() {
    setShow(g50code());
  }
  return (
    <>
      <Navigation />
      <div class="company" id="company" />
      <div class="price" id="price">
        <div className="topmobiles">
          <button id="l10" onClick={l10} className="btn filtername btn-primary">
            &#60;10,000
          </button>
          <button id="g10" onClick={g10} className="btn filtername btn-primary">
            &#62;10,000
          </button>
          <button id="g25" onClick={g25} className="btn filtername btn-primary">
            &#62;25,000
          </button>
          <button id="g50" onClick={g50} className="btn filtername btn-primary">
            &#62;50,000
          </button>
        </div>
      </div>
      <div className="mobiles">{show}</div>
    </>
  );
};

export default Products;
