import React from "react";
import { Link, useParams } from "react-router-dom";
import "./index.css";
import "bootstrap/dist/css/bootstrap.min.css";
import "react-responsive-carousel/lib/styles/carousel.min.css";
import Mobiles from "./Mobiles";
import CartDetail from "./CartDetail";
import Navigation from "./Navigation";
var Carousel = require("react-responsive-carousel").Carousel;

const ProductDetail = () => {
  const { productname } = useParams();
  const { companyname } = useParams();
  return Mobiles.map((v, i) => {
    const title = v.name.split(" ").join("-");
    if (productname === title && v.company === companyname) {
      return (
        <>
          <Navigation />
          <div className="container productdetail">
            <div className="row">
              <div className="col-md-4" style={{ margin: "20px 0" }}>
                <Carousel
                  showArrows={true}
                  showThumbs={false}
                  autoPlay={true}
                  interval={3000}
                  infiniteLoop={true}
                  className="carousel"
                >
                  {v.detail.images.map(v1 => {
                    return (
                      <>
                        <img src={v1} alt={title} className="d-flex w-100" />
                      </>
                    );
                  })}
                </Carousel>
              </div>
              <div className="col-md-8" style={{ margin: "20px 0" }}>
                <h1>{v.name}</h1>
                <div className="d-flex align-items-center">
                  <p style={{ margin: "10px" }}>
                    Company : <b>{v.company}</b>
                  </p>
                  <p style={{ margin: "10px" }}>
                    Price : <b>{v.price} </b>
                  </p>
                  <Link to="/cart">
                    <button
                      style={{ margin: "10px" }}
                      className="btn btn-primary"
                      onClick={() => {
                        return (
                          <>
                            {CartDetail.push({
                              image: v.image,
                              name: v.name,
                              company: v.company,
                              price: v.price
                            })}
                          </>
                        );
                      }}
                    >
                      Add to Cart
                    </button>
                  </Link>
                </div>
                <h3>Performance</h3>
                <div className="d-flex flex-column">
                  {v.detail.performance.map(p => {
                    return (
                      <>
                        <ul>
                          <li style={{ marginLeft: "10px", marginTop: "-2px" }}>
                            {p}
                          </li>
                        </ul>
                      </>
                    );
                  })}
                </div>
                <h3>Camera</h3>
                <div className="d-flex flex-column">
                  {v.detail.camera.map(p => {
                    return (
                      <>
                        <ul>
                          <li style={{ marginLeft: "10px", marginTop: "-2px" }}>
                            {p}
                          </li>
                        </ul>
                      </>
                    );
                  })}
                </div>
                <div className="d-md-flex flex-wrap">
                  <div className="display">
                    <h3>Display</h3>
                    <div className="d-flex flex-column">
                      {v.detail.display.map(p => {
                        return (
                          <>
                            <ul>
                              <li
                                style={{
                                  marginLeft: "10px",
                                  marginTop: "-2px"
                                }}
                              >
                                {p}
                              </li>
                            </ul>
                          </>
                        );
                      })}
                    </div>
                  </div>
                  <div className="battery" style={{ margin: "auto" }}>
                    <h3>Battery</h3>
                    <div className="d-flex flex-column">
                      {v.detail.battery.map(p => {
                        return (
                          <>
                            <ul>
                              <li
                                style={{
                                  marginLeft: "10px",
                                  marginTop: "-2px"
                                }}
                              >
                                {p}
                              </li>
                            </ul>
                          </>
                        );
                      })}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </>
      );
    } else {
      return false;
    }
  });
};

export default ProductDetail;
